@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
body {
  margin: 0;
}

p {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

.App {
  background-color: #fafafa;
  padding-bottom: 5rem;
}

.container {
  padding: 0 5%;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
  height: 95px;
  background-color: #ffffff;
}

.booksLink, .categoryLink {
  height: 16px;
  font-family: 'Montserrat', sans-serif;
  font-size: 13px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 1.9px;
  color: #121212;
  text-decoration: none;
}

.booksLink {
  padding: 0 45px;
}

.categoryLink {
  opacity: 0.5;
}

.userCircle {
  text-align: center;
  line-height: 47px;
  width: 45px;
  height: 45px;
  border-radius: 100%;
  border: solid 1px #e8e8e8;
}

.fa-user {
  width: 17px;
  height: 17px;
  color: #0290ff;
}

.logoLink {
  width: 240px;
  height: 37px;
  font-family: 'Montserrat', sans-serif;
  font-size: 30px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #0290ff;
  text-decoration: none;
}

.BookList {
  padding: 40px 0;
}

.bookCard {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  margin-bottom: 15px;
  padding: 30px;
}

.bookCardLeft {
  width: 400px;
}

.bookCardMiddle {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 200px;
}

.bookCardRight {
  border-left: 2px solid #e8e8e8;
  padding: 0 50px;
  width: 200px;
}

.bookCategory {
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  margin-bottom: 4px;
}

.bookTitle {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 22px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #121212;
  margin-bottom: 0px;
  text-decoration: none;
}

.bookAuthor {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 14px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
  margin-bottom: 20px;
}

.bookLink {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 14px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
  margin-bottom: 0px;
  text-decoration: none;
}

.bookLink:hover {
  text-decoration: underline;
}

.bookLink:nth-child(1) {
  padding-right: 15px;
  border-right: 2px solid #e8e8e8;
}

.bookLink:nth-child(2) {
  margin: 0 15px;
}

.bookLink:nth-child(3) {
  padding-left: 15px;
  border-left: 2px solid #e8e8e8;
}

.bookProgressBar {
  height: 68px;
}

.bookProgressNumber {
  font-family: 'Montserrat', sans-serif;
  font-size: 32px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  margin-bottom: 1px;
}

.bookProgressComplete {
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.bookChapterHeader {
  opacity: 0.5;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 13px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  margin-bottom: 7px;
}

.bookChapter {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 16px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.4px;
  color: #121212;
  margin-bottom: 23px;
}

.progressBtn {
  width: 184px;
  height: 33px;
  border-radius: 3px;
  background-color: #0290ff;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 13px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
  color: #ffffff;
}

.filter {
  padding: 10px 0;
}

.filterTag {
  width: 49px;
  height: 18px;
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  margin-right: 15px;
}

.filterSelector {
  width: 150px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  color: #c4c4c4;
}

.Form {
  align-items: center;
  border-top: solid 2px #e8e8e8;
  margin-bottom: 50px;
}

.row {
  margin-bottom: 16px;
}

.col {
  margin-right: 16px;
}

.formHeader {
  height: 24px;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #888888;
  margin-top: 30px;
  margin-bottom: 20px;
}

.formHeaderSub {
  font-size: 12px;
  margin-left: 12px;
}

.flexDiv {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bookFormInput {
  width: 50%;
  padding: 15px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  color: gray;
}

.fieldLabel {
  padding-right: 8px;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  color: gray;
}

.bookFormValue {
  padding: 15px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  color: gray;
}

.commentFormInput {
  width: 35%;
  padding: 15px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  color: gray;
}

.centerDiv {
  display: flex;
  justify-content: center;
  align-items: center;
}

.commentFormTextBox {
  margin-top: 18px;
  width: 100%;
  padding: 15px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  color: gray;
}

.formInput::-webkit-input-placeholder {
  color: #c4c4c4;
}

.formInput::-ms-input-placeholder {
  color: #c4c4c4;
}

.formInput::placeholder {
  color: #c4c4c4;
}

.formSelector {
  width: 285px;
  padding: 15px;
  height: 50px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  color: #c4c4c4;
}

.formBtn {
  width: 184px;
  height: 45px;
  border-radius: 3px;
  background-color: #0290ff;
  color: #ffffff;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 13px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
}

.BookView {
  padding: 40px 0;
}

.commentCard {
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #ffffff;
  margin-bottom: 15px;
  padding: 30px;
}

.commentCardLeft {

}

.commentCardRight {
  text-align: right;
}

.commentCardBottom {
  border-top: 2px solid #e8e8e8;
}

.commentUser, .commentRating {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.4px;
  color: #121212;
  margin: 10px 0;
}

.commentContent {
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  margin: 10px 0;
}

